/* eslint-disable */



export const displayMap = (locations) => {
  mapboxgl.accessToken =
    'pk.eyJ1IjoiYWJkZXZlbG9wbWFwczQyIiwiYSI6ImNrZ2gydThoYTFlYTQycnMxb2syN3cydjEifQ.FRsUz6xtU5T5X4hSN3v5dA';
  var map = new mapboxgl.Map({
    container: 'map',
    style: 'mapbox://styles/abdevelopmaps42/ckgi0g2lj23y319obi0zougdl',
    scrollZoom: false,
    // center: [ -118.243683, 34.052235],
    // zoom: 10,
    // interactive: false,
  });
  const bounds = new mapboxgl.LngLatBounds();

  locations.forEach((loc) => {
    // Create marker
    const el = document.createElement('div');
    el.className = 'marker';

    // Add marker
    new mapboxgl.Marker({
      element: el,
      anchor: 'bottom',
    })
      .setLngLat(loc.coordinates)
      .addTo(map);

    // Add popup
    new mapboxgl.Popup({
      offset: 30,
    })
      .setLngLat(loc.coordinates)
      .setHTML(`<p>Day ${loc.day}: ${loc.description}</p>`)
      .addTo(map);

    // Extend map bounds to include current location
    bounds.extend(loc.coordinates);
  });

  map.fitBounds(bounds, {
    padding: {
      top: 200,
      bottom: 150,
      left: 100,
      right: 100,
    },
  });
};
